<template>
    <div>
        
    </div>
</template>

<script>
    import axios from 'axios'
    export default {
        name : 'Books',

        data() {
            return {
                books: [],        
            }
        },

        mounted() {
            axios.get('/books')
            .then(res => {
                console.log(res)
                this.books = res.data
            })
            .catch( err => {
                console.log(err)
            })
        },
    }
</script>

